.fileUrl {
  background-color: #000a29;
  height: auto;
  min-height: 100vh;
}

.fileUrl .nav-link {
  text-align: center;
  color: rgb(255, 255, 255);
  font-weight: 600;
  font-size: medium;
}

.fileUrl .nav-item .downloadButton {
  background-color: #045de9;
  background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
  margin: 80px 10px 20px 10px;
  width: 30vh;
  margin-left: 44%;
  padding: 10px;
  border-radius: 40px;
  box-shadow: 5px 10px rgba(255, 255, 255, 0.15);
}
.fileUrl .nav-item .disableButton {
  background-color: #000000;
  background-image: linear-gradient(315deg, #000000 0%, #7f8c8d 74%);
  align-self: center;
  margin: 80px 10px 20px 10px;
  width: 30vh;
  margin-left: 44%;
  padding: 10px;
  border-radius: 40px;
  box-shadow: 5px 10px rgba(255, 255, 255, 0.15);
}

.fileUrl .urlinput {
  width: 50%;
  height: 5vh;
  margin: 10vh 0vh 2vh 0vh;
  border: 2px solid rgb(241, 167, 8);
}

.fileUrl .urldiv {
  margin-left: 30%;
}

.fileUrl label {
  color: white;
  background-color: #fbb034;
  background-image: linear-gradient(315deg, #fbb034 0%, #ffdd00 74%);
  padding: 1vh;
  width: 8%;
  text-align: center;
  font-weight: bold;
}

@media (max-width: 767.98px) {
  .fileUrl .urldiv {
    margin-left: 10%;
  }
  .fileUrl label {
    color: white;
    background-color: #fbb034;
    background-image: linear-gradient(315deg, #fbb034 0%, #ffdd00 74%);
    padding: 1vh;
    width: 15%;
    text-align: center;
    font-weight: bold;
  }
  .fileUrl .urlinput {
    width: 70%;
    height: 5vh;
    margin: 10vh 0vh 2vh 0vh;
    border: 2px solid rgb(241, 167, 8);
  }
  .fileUrl .nav-item .downloadButton {
    background-color: #045de9;
    background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
    margin: 80px 10px 20px 10px;
    width: 30vh;
    margin-left: 30%;
    padding: 10px;
    border-radius: 40px;
    box-shadow: 5px 10px rgba(255, 255, 255, 0.15);
  }
  .fileUrl .nav-item .disableButton {
    background-color: #000000;
    background-image: linear-gradient(315deg, #000000 0%, #7f8c8d 74%);
    align-self: center;
    margin: 80px 10px 20px 10px;
    width: 30vh;
    margin-left: 30%;
    padding: 10px;
    border-radius: 40px;
    box-shadow: 5px 10px rgba(255, 255, 255, 0.15);
  }
}
