.newpreview {
  background-color: #000a29;
  height: auto;
  min-height: 100vh;
  overflow-x: hidden;
}
.newpreview .navbar-brand {
  color: yellow;
  font-weight: bold;
  font-family: "Times New Roman", Times, serif;
  font-size: x-large;
}

.newpreview button {
  margin: 0 ;
  padding: 4px;
}

.dataframeview {
  width: 100%;
}

.left {
  padding-left: 3vh;
  padding-top: 0vh;
}

.newpreview .nav-item .uploadButton {
  background-color: #f50057;
  margin: 20px 20px 20px 0px;
  width: 10vh;
  margin-left: -2vh;
  padding: 5px;
  border-radius: 20px;
  box-shadow: 5px 10px rgba(223, 223, 223, 0.15);
}
.newpreview .nav-item .workButton {
  background-color: #f50057;
  margin: 0px 10px 6px 0px;
  width: 12vh;
  margin-left: 0vh;
  padding: 0px;
  border-radius: 20px;
  box-shadow: 3px 6px rgba(223, 223, 223, 0.15);
}
.newpreview .nav-item .filterButton {
  float: right;
  background-color: #f50057;
  margin: 0px 10px 6px 0px;
  width: 18vh;
  margin-left: 0vh;
  padding: 0px;
  border-radius: 20px;
  box-shadow: 3px 6px rgba(223, 223, 223, 0.15);
}
.newpreview #btn1 {
  background-color: #00b712;
  background-image: linear-gradient(315deg, #00b712 0%, #5aff15 74%);
}
.newpreview #btn2 {
  background-color: #fbb034;
  background-image: linear-gradient(315deg, #f19c12 0%, #ffdd00ef 74%);
}
.newpreview #btn3 {
  background-color: rgb(139, 61, 213);
  
}
.newpreview .nav-item .disableButton {
  background-color: #000000;
  background-image: linear-gradient(315deg, #000000 0%, #7f8c8d 74%);

  align-self: center;

  margin: 20px 20px 20px 0px;
  width: 10vh;
  margin-left: -2vh;
  padding: 5px;
  border-radius: 20px;
  box-shadow: 5px 10px rgba(255, 255, 255, 0.15);
}

 .newpreview .navbar-nav .nav-link {
  text-align: center;
  color: rgb(255, 255, 255);
  font-weight: 500;
  font-size: small;
}

.newpreview .navbar-nav .nav-link:hover {
  font-weight: 500;
  color: rgb(0, 0, 0);
}

.newpreview .navbar-nav .nav-link:link {
  text-decoration: none;
  color: white;
}

.newpreview .navbar-nav .nav-link:visited {
  text-decoration: none;
  color: white;
}

.newpreview .navbar-nav .nav-link:active {
  text-decoration: none;
  color: white;
}

.newpreview .fas {
  color: rgb(255, 255, 255);
  margin: 1vh 1vh 1vh 2vh;
}

.newpreview .box {
  background-color: #1d1e33;
  margin: 50px 10px 20px 0px;
  width: 7vh;
  margin-left: 3vh;
  padding: 5px;
  border-radius: 10px;
  box-shadow: 5px 10px rgba(255, 255, 255, 0.15);
  height: 100%;
}

.newpreview .nav-item .downloadButton {
  background-color: #045de9;
  background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
  align-self: center;
  text-align: center;

  margin: 80px 10px 20px 10px;
  width: 12vh;
  margin-left: 5%;

  padding: 8px;
  border-radius: 40px;
  box-shadow: 5px 10px rgba(255, 255, 255, 0.15);
}

.newpreview .progressbar {
  margin-top: 5vh;

  margin-right: 10px;
}

.query {
  margin-bottom: 2vh;
}

.query input {
  border: 1px solid #eeeeee;
  width: 90%;
  padding: 14px;
  flex-grow: 1;
  border-radius: 10px;
  height: 2px;
}

.query button {
  font-size: 20px;
  border-radius: 10px;
  border-color: #09c6f9;
  background-color: #09c6f9;
  box-shadow: 5px 10px rgba(0, 255, 255, 0.15);
  text-align: center;
  padding: 0;
  width: 9%;
  margin-left: 1%;
}



.filterButton button {
  background-color: blueviolet;
  color: white;
  margin-bottom: 1px;
}

#demo-simple-select {
  color: aliceblue;
  background-color: #09c6f9;
}

#demo-simple-select-label {
  color: palevioletred;
}

#searchform {
  margin-top: -16px;
}

.searchmenu p {
  color: bisque;
  margin-left: 2vh;
  margin-bottom: 1px;
}

.query label {
  color: bisque;
}
.method2 {
  max-height: 50vh;
  overflow-y: scroll;
}
