.customize {
    font-family: 'Playfair Display', serif;
    font-weight: bold;
}
.customize .nav-item .downloadButton {
    background-color: #045de9;
    background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
    align-self: center;


    
    margin: 10px 10px 20px 10px;
    width: 30vh;
    margin-left: 44%;
    
    padding: 10px;
    border-radius: 40px;
    box-shadow: 5px 10px rgba(0,0,0,0.15);
   }

   .customize  .nav-link {
    text-align: center;
    color: rgb(255, 255, 255);
    font-weight: 600;
    font-size: medium;
}
.customize .nav-item .disableButton {
      
    background-color: #000000;
    background-image: linear-gradient(315deg, #000000 0%, #7f8c8d 74%);


    align-self: center;


    
    margin: 10px 10px 20px 10px;
    width: 30vh;
    margin-left: 44%;
    
    padding: 10px;
    border-radius: 40px;
    box-shadow: 5px 10px rgba(255,255,255,0.15);
   }

